import React, { useEffect, useState } from "react";
import axios from "axios";
import Chart from "react-apexcharts";

// MUI
import CircularProgress from "@material-ui/core/CircularProgress";
import withStyles from "@material-ui/core/styles/withStyles";

const styles = {
    flex: {
        display: "flex",
        justifyContent: "center"
    }
}

const DayOneChart = ({ country, classes }) => {
    const [state, setState] = useState({});
    useEffect(() => {
        axios
            .get(
                `https://api.covid19api.com/total/dayone/country/${country}`
            )
            .then(res => {
                const { data } = res;
                console.log(data);
                setState({
                    chart: {
                        series: [
                            {
                                name: "Total Confirmed Cases",
                                data: data.map(item => item.Confirmed),
                            },
                            {
                                name: "Total Deaths",
                                data: data.map(item => item.Deaths),
                            },
                            {
                                name: "Total Recovered",
                                data: data.map(item => item.Recovered),
                            },
                        ],
                        options: {
                            colors: ["#000000", "#DA1F33", "#82FF6B"],
                            chart: {
                                height: 350,
                                type: "line",
                                zoom: {
                                    enabled: false,
                                },
                                toolbar: {
                                    show: false
                                }
                            },
                            dataLabels: {
                                enabled: false
                            },
                            stroke: {
                                curve: "straight",
                                colors: ["black", "#DA1F33", "#3BEE5D"]
                            },
                            title: {
                                text: `Covid-19 statistics ${country.charAt(0).toUpperCase() + country.slice(1)}`,
                                align: "center",
                                style: {
                                    fontWeight: 500,
                                    fontFamily: "-apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif"
                                }
                            },
                            grid: {
                                row: {
                                    colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
                                    opacity: 0.5,
                                },
                            },
                            xaxis: {
                                type: "datetime",
                                categories: data.map(item =>
                                    new Date(item.Date).toDateString()
                                ),
                                labels: {
                                    show: false
                                },
                            },
                        },
                    },
                });
            });
    }, [country]);
    return state.chart ? (
        <Chart options={state.chart.options} series={state.chart.series} />
    ) : (
        <div className={classes.flex}><CircularProgress/></div>
    );
};

export default withStyles(styles)(DayOneChart);
