import React, { Component } from "react";
import axios from "axios";

// Components
import DayOneChart from "../components/dayOneChart";

// MUI
import Grid from "@material-ui/core/Grid";
import TypoGraphy from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import withStyles from "@material-ui/core/styles/withStyles";
import CircularProgress from "@material-ui/core/CircularProgress";

const styles = theme => ({
    leftGrid: {
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        marginTop: "2rem",
    },
    comboBox: {
        margin: "2rem",
    },
    chart: {
        [theme.breakpoints.up("md")]: {
            margin: "2rem",
        },
    },
    stats: {
        [theme.breakpoints.down("sm")]: {
            fontSize: "1.5rem",
        },
    },
});

class homePage extends Component {
    state = {
        selectedIndex: null,
        data: {
            Countries: [],
            Global: {},
        },
        loading: true,
    };

    handleChange = value => {
        const { Countries } = this.state.data;
        if (value && value.Country === "Global")
            this.setState({ selectedIndex: null });
        else if (Countries.includes(value)) {
            this.setState({ selectedIndex: Countries.indexOf(value) });
        }
    };

    componentDidMount() {
        this.getData();
    }

    getData = () => {
        axios
            .get("https://api.covid19api.com/summary")
            .then(data => {
                this.setState({ data: data.data, loading: false });
            })
            .catch(err => {
                console.error(err);
                setTimeout(() => {
                    this.getData();
                }, 1000);
            });
    };

    render() {
        const { classes } = this.props;
        const {
            selectedIndex,
            loading,
            data: { Countries, Global },
        } = this.state;
        return (
            <div className={loading ? "homepage loading" : "homepage"}>
                {!loading && (
                    <Grid container>
                        <Grid item sm={5} xs={12} className={classes.leftGrid}>
                            <TypoGraphy variant="h3">
                                {selectedIndex !== null
                                    ? Countries[selectedIndex].Country
                                    : "Global"}
                            </TypoGraphy>
                            <TypoGraphy className={classes.stats} variant="h4">
                                Total confirmed cases:{" "}
                                {selectedIndex !== null
                                    ? Countries[selectedIndex].TotalConfirmed
                                    : Global.TotalConfirmed}
                            </TypoGraphy>
                            <TypoGraphy className={classes.stats} variant="h4">
                                Total deaths:{" "}
                                {selectedIndex !== null
                                    ? Countries[selectedIndex].TotalDeaths
                                    : Global.TotalDeaths}
                            </TypoGraphy>
                            <TypoGraphy className={classes.stats} variant="h4">
                                Total recovered:{" "}
                                {selectedIndex !== null
                                    ? Countries[selectedIndex].TotalRecovered
                                    : Global.TotalRecovered}
                            </TypoGraphy>
                        </Grid>
                        <Grid item sm={7} xs={12}>
                            <Autocomplete
                                className={classes.comboBox}
                                autoComplete
                                autoHighlight
                                options={[
                                    {
                                        Country: "Global",
                                        ...this.state.data.Global,
                                    },
                                    ...this.state.data.Countries,
                                ]}
                                getOptionLabel={option => option.Country}
                                onChange={(event, value) =>
                                    this.handleChange(value)
                                }
                                renderInput={params => (
                                    <TextField
                                        {...params}
                                        label="Country"
                                        variant="outlined"
                                    />
                                )}
                            />
                            {selectedIndex !== null && (
                                <div className={classes.chart}>
                                    <DayOneChart
                                        country={Countries[selectedIndex].Slug}
                                    />
                                </div>
                            )}
                        </Grid>
                    </Grid>
                )}
                {loading && <CircularProgress size={65} />}
            </div>
        );
    }
}

export default withStyles(styles)(homePage);
