import React, { Component } from "react";
import axios from "axios";

// MUI
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Typography from "@material-ui/core/Typography";
import CircularProgress from "@material-ui/core/CircularProgress";

// Icons
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";
import LastPageIcon from "@material-ui/icons/LastPage";
import FirstPageIcon from "@material-ui/icons/FirstPage";

import withStyles from "@material-ui/core/styles/withStyles";

const styles = {
    bold: {
        fontWeight: "bold",
    },
    pagination: {
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        marginLeft: "3%",
        marginRight: "3%",
    },
};

class statsPage extends Component {
    state = {
        page: 1,
        sort: "Slug",
        direction: "desc",
    };
    componentDidMount() {
        this.getData();
    }
    getData = () => {
        axios
            .get("https://api.covid19api.com/summary")
            .then(data => {
                this.setState({ data: data.data, loading: false });
            })
            .catch(err => {
                console.error(err);
                setTimeout(() => {
                    this.getData();
                }, 1000);
            });
    };
    handleClick = e => {
        const sort = e.currentTarget.id;
        if (this.state.sort === sort)
            this.setState({
                direction: this.state.direction === "desc" ? "asc" : "desc",
            });
        else this.setState({ sort: e.currentTarget.id, order: "asc" });
    };
    sort = countries => {
        let sorted = countries;
        const { sort, direction } = this.state;
        if (direction === "asc")
            sorted = countries.sort((a, b) => (a[sort] > b[sort] ? -1 : 1));
        else if (direction === "desc")
            sorted = countries.sort((a, b) => (a[sort] > b[sort] ? 1 : -1));

        return sorted;
    };
    comparison = (a, b) => {
        const countryA = a.slug;
        const countryB = b.slug;
        if (countryA > countryB) return 1;
        else if (countryA < countryB) return -1;
    };
    render() {
        if (!this.state.data)
            return (
                <div className="statspage loading">
                    <CircularProgress size={65} />
                </div>
            );

        const sorted = this.sort(this.state.data.Countries);

        // Paginate
        const maxPages = Math.ceil(sorted.length / 10);
        const { page } = this.state;
        if (page > maxPages) this.setState({ page: maxPages });
        else if (page < 1) this.setState({ page: 1 });
        const countriesToShow = sorted.slice(page * 10 - 10, page * 10);

        const { classes } = this.props;
        return (
            <div className="statspage">
                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell onClick={this.handleClick} id="Slug">
                                    <TableSortLabel
                                        active={this.state.sort === "Slug"}
                                        direction={this.state.direction}
                                    />
                                    Country
                                </TableCell>
                                <TableCell
                                    onClick={this.handleClick}
                                    id="TotalConfirmed"
                                >
                                    <TableSortLabel
                                        active={
                                            this.state.sort === "TotalConfirmed"
                                        }
                                        direction={this.state.direction}
                                    />
                                    Total Confirmed cases
                                </TableCell>
                                <TableCell
                                    onClick={this.handleClick}
                                    id="NewConfirmed"
                                >
                                    <TableSortLabel
                                        active={
                                            this.state.sort === "NewConfirmed"
                                        }
                                        direction={this.state.direction}
                                    />
                                    New Confirmed cases
                                </TableCell>
                                <TableCell
                                    onClick={this.handleClick}
                                    id="TotalDeaths"
                                >
                                    <TableSortLabel
                                        active={
                                            this.state.sort === "TotalDeaths"
                                        }
                                        direction={this.state.direction}
                                    />
                                    Total Confirmed Deaths
                                </TableCell>
                                <TableCell
                                    onClick={this.handleClick}
                                    id="NewDeaths"
                                >
                                    <TableSortLabel
                                        active={this.state.sort === "NewDeaths"}
                                        direction={this.state.direction}
                                    />
                                    New Confirmed Deaths
                                </TableCell>
                                <TableCell
                                    onClick={this.handleClick}
                                    id="TotalRecovered"
                                >
                                    <TableSortLabel
                                        active={
                                            this.state.sort === "TotalRecovered"
                                        }
                                        direction={this.state.direction}
                                    />
                                    Total Recovered
                                </TableCell>
                                <TableCell
                                    onClick={this.handleClick}
                                    id="NewRecovered"
                                >
                                    <TableSortLabel
                                        active={
                                            this.state.sort === "NewRecovered"
                                        }
                                        direction={this.state.direction}
                                    />
                                    New Recovered
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {countriesToShow.map(country => (
                                <TableRow key={country.CountryCode}>
                                    <TableCell
                                        component="th"
                                        scope="row"
                                        className={classes.bold}
                                    >
                                        {country.Country}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {country.TotalConfirmed}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {country.NewConfirmed}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {country.TotalDeaths}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {country.NewDeaths}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {country.TotalRecovered}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {country.NewRecovered}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                    <div className={classes.pagination}>
                        <Typography variant="body1">
                            Page: {this.state.page}
                        </Typography>
                        <div>
                            <IconButton
                                onClick={() => this.setState({ page: 1 })}
                            >
                                <FirstPageIcon />
                            </IconButton>
                            <IconButton
                                onClick={() =>
                                    this.setState({
                                        page:
                                            this.state.page > 1
                                                ? this.state.page - 1
                                                : this.state.page,
                                    })
                                }
                            >
                                <KeyboardArrowLeft />
                            </IconButton>
                            <IconButton
                                onClick={() =>
                                    this.setState({
                                        page: this.state.page + 1,
                                    })
                                }
                            >
                                <KeyboardArrowRight />
                            </IconButton>
                            <IconButton
                                onClick={() =>
                                    this.setState({ page: maxPages })
                                }
                            >
                                <LastPageIcon />
                            </IconButton>
                        </div>
                    </div>
                </TableContainer>
            </div>
        );
    }
}

export default withStyles(styles)(statsPage);
